.quantity-controls {
  border-radius: 10px;
  background-color: #176b87;
  color: black;
}
.body {
  background-color: #eeeeee;
}
/*This styles the container for each route and gives it a border*/
.item {
  width: 80%; /* Adjust width as needed */
  max-width: 600px; /* Optional: Set a maximum width */
  margin: 10px auto;
  padding: 20px;
  border-radius: 10px; /* Add rounded corners */
  background-color: #f5f5f5; /* Light gray background */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Subtle shadow */
}
/*style the main heading of the page*/
.TicketTitle {
  font-weight: bolder;
  font-size: 3em;
  color: #053b50;
  display: flex; /* Make the wrapper a flex container */
  align-items: center; /* Align items vertically to the center */
  justify-content: center; /* Center items horizontally within the wrapper */
  margin-top: 12rem;
}
.Title {
  font-weight: bolder;
  font-size: 3em;
  color: #053b50;
  display: flex; /* Make the wrapper a flex container */
  align-items: center; /* Align items vertically to the center */
  justify-content: center; /* Center items horizontally within the wrapper */
  margin-top: 20rem;
}
.itemTitle {
  align-items: self-start;
  font-size: 1.5em;
  font-weight: bolder;
  margin-top: 1rem;
}

.ticket p {
  margin-bottom: 5px; /* Add spacing between info lines */
}

.quantity-controls {
  color: #eeeeee;
  display: flex; /* Make controls horizontal */
  margin-left: 0;
  margin-right: 0;
  align-items: center; /* Align buttons vertically */
  margin-top: 10px; /* Add spacing above controls */
}

.quantity-controls button {
  margin-left: 0;
  margin-right: 0;
  color: #eeeeee;
  border: none;
  background-color: transparent;
  cursor: pointer; /* Indicate clickable behavior */
  padding: 5px; /* Add some padding around icons */
}
.deleteItem {
  padding-left: 55%;
  display: flex; /* Make the section a flex container */
  justify-content: flex-end; /* Align items to the right */
}
.inputControl {
  display: flex; /* Make the section a flex container */
  justify-content: flex-end; /* Align items to the right */
  background-color: rgba(255, 0, 0, 0);
}

.quantity-controls input {
  margin-left: 0;
  margin-top: 6%;
  display: flex; /* Make the section a flex container */
  justify-content: flex-end; /* Align items to the right */
  height: 15px;
  font-weight: bold;
  color: black;
  border: 1px solid #176b87; /* Light border for input */
  border-radius: 3px; /* Rounded corners for input */
  padding: 5px; /* Add some padding inside input */
  width: 30px; /* Adjust width for quantity input */
  text-align: center; /* Center text within input */
}

/*these are button stylings for the continue shopping , and checkout buttons*/
.btnShop,
.btnChk,
.btnShop2 {
  border-radius: 10px;
  margin-top: 20px;
  margin-right: 20px;
  margin-bottom: auto;
  margin-left: 25%;
  color: white;
  font-size: 20px;
  font-weight: bold;
  background-color: #1cbdb2;
}
.btnShop2 {
  margin-left: 10rem;
}
/*Styles the text for that has the subtoatl amont moves it to the left under the box*/
.total {
  font-weight: bold;
  margin-left: 40%;
  margin-right: 30%;
  float: right;
  font-size: 1.5rem;
}

/*STyles the text for the empty cart*/
.empty {
  font-size: 2em;
  font-weight: bold;
  margin-left: 4em;
  padding: 5%;
}
